.footer
  background-color: #110F1D
  color: #fff
  position: relative
  z-index: 1
  overflow: hidden
  &__smoke
    position: absolute
    bottom: 0
    left: 50%
    transform: translateX(-50%)
    +md 
      display: none
  &__wrap
    padding: 52px 0 44px
    display: flex
    justify-content: space-between
    position: relative
    +md 
      display: block
      padding: 32px 0 38px
  &__bullet
    position: absolute
    bottom: 0
    right: 0
    transform: translateX(50%)
    z-index: -1
    +md  
      display: none
  &__main
    padding-left: 100px
    padding-right: 30px
    position: relative
    +lg
      padding-left: 0
    +md 
      padding-right: 0
      text-align: center
      margin-bottom: 10px
  &__copy
    display: block
    margin-bottom: 12px
    +lg 
      display: inline-flex
      align-items: center
    +md 
      margin-bottom: 5px
    +xs
      font-size: 12px
  &__logo
    position: absolute
    left: 0
    top: 0
    +lg 
      position: static
      width: 30px
      height: 30px
      margin-right: 10px
      .icon
        width: 100%
        height: 100%
  &__info
    color: #78758C
    margin-bottom: 28px
    +md 
      margin-bottom: 10px
    +sm 
      font-size: 10px
    p
      margin: 0
  &__about
    color: #4D4A64
    +sm 
      font-size: 13px
    p
      margin: 0
  &__second
    max-width: 580px
    width: 100%
    +lg 
      max-width: 500px
    +md 
      max-width: none
  &__box
    display: flex
    justify-content: space-between
    margin-bottom: 40px
    +md 
      flex-direction: column
      margin-bottom: 10px
  &__payment
    max-width: 250px
    display: flex
    align-items: center
    flex-wrap: wrap
    margin-bottom: -10px
    +md 
      margin: 0 auto
  &__payment-image
    margin-right: 18px
    margin-bottom: 10px
    &:nth-child(3n)
      margin-right: 0
  &__links
    list-style: none
    padding: 0
    margin: 0
    display: flex
    +md 
      order: -1
      justify-content: center
      margin-bottom: 16px
  &__links-item
    &:not(:last-child)
      margin-right: 30px
  &__link
    display: flex
    align-items: center
    color: #4D4A64
    .icon
      margin-right: 10px

.footer-menu
  &__list
    list-style: none
    padding: 0
    margin: 0
    display: flex
    +md 
      display: block
  &__item
    &:not(:last-child)
      margin-right: 30px
      +md 
        margin: 0 0 15px
  &__link
    display: block
    font-size: 14px
    text-transform: uppercase
    color: #EF436C
    +md 
      text-align: center
    +sm 
      font-size: 10px

.container
  max-width: 1360px
  padding: 0 20px
  margin: 0 auto
