@import "./normalize"
@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"
  
* 
    box-sizing: border-box
    // min-height: 0.001vw; //  Fixes Safari bug with viewport units in calc()
:root
    --yellow: #FFE600
h1,h2,h3,h4,h5,h6
    margin: 0
a
    text-decoration: none
    color: inherit
body
    background-color: #000
    font-family: "FSElliotPro", Arial, sans-serif
    font-weight: 700
    width: 100%
    height: 100%
    min-height: 100vh
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale


img, iframe
    vertical-align: top
.row
    display: flex
    flex-wrap: wrap
.row-45
    margin-left: -45px
    margin-right: -45px
    +md
        margin-left: -10px
        margin-right: -10px
    &>[class*="col"]
        padding-left: 45px
        padding-right: 45px
        +md
            padding-left: 10px
            padding-right: 10px
.row-15
    margin-left: -15px
    margin-right: -15px
    +md
        margin-left: -10px
        margin-right: -10px
    &>[class*="col"]
        padding-left: 15px
        padding-right: 15px
        +md
            padding-left: 10px
            padding-right: 10px
@media (min-width: 768px)
    .col-md-4
        flex: 0 0 auto
        width: 33.33333333%
    .col-md-5
        flex: 0 0 auto
        width: 41.66666667%
        position: relative
    .col-md-7
        flex: 0 0 auto
        width: 58.33333333%
        position: relative
+sm
   .col-md-4, .col-md-5, .col-md-7
        flex: 0 0 auto
        width: 100%
.main
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    color: #fff
    height: 100%
    min-height: 100vh
    overflow: hidden
    position: relative
    padding-top: 120px
    padding-bottom: 100px
    +xl
        padding-top: 60px
        padding-bottom: 60px
.page-bg
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    img
        width: 100%
        height: 100%
        object-fit: cover
.paralax-bg 
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    mix-blend-mode: screen
    opacity: .8
    +sm
        opacity: 1
.paralax-bg-case-1
    .layer
        left: 0
        right: 0
        top: 0
        bottom: 0
        position: absolute !important
        img
            width: 110%
            height: 110%
            object-fit: cover
            position: absolute
            left: 50%
            top: 50%
            transform: translate(-50%, -25%)
            +sm
                width: 100%
                left: 0
                right: 0
                top: 0
                bottom: 0
                transform: none
                position: absolute
                display: block
.paralax-bg-case-2
    .layer
        left: 0
        right: 0
        top: 0
        bottom: 0
        position: absolute !important
        img
            width: 110%
            height: 110%
            object-fit: cover
            position: absolute
            left: 50%
            top: 50%
            transform: translate(-50%, -50%)
            +sm
                width: 100%
                left: 0
                right: 0
                top: 0
                bottom: 0
                transform: none
                position: absolute
                display: block
.decor-left
    position: absolute
    left: 0
    top: 7%
    width: 264px
    +xl
        width: 200px
    +sm
        width: 140px
        top: 15%
    +xs 
        width: 126px
    img
        width: 100%
.decor-right
    position: absolute
    right: 0
    top: 4%
    width: 248px
    +xl
        width: 160px
    +sm
        width: 100px
        top: 40%
    img
        width: 100%
.box-content
    position: relative
    z-index: 10
    text-align: center
    padding-left: 100px
    padding-right: 100px
    +xl
        width: 100%
    +md
        padding-left: 20px
        padding-right: 20px
.welcome-text
    margin-bottom: 60px
    transform: translateY(200%)
    opacity: 0
    +xl
        margin-bottom: 40px
    +md
        padding-left: 50px
        padding-right: 50px
        margin-bottom: 40px
    +sm
        margin-bottom: 70px
        padding-left: 0
        padding-right: 0
        transform: none !important

    h1
        color: var(--yellow)
        text-transform: uppercase
        line-height: 125%
        font-size: 60px
        margin-bottom: 10px
        text-shadow: 0px 0px 15px rgba(226, 228, 255, 0.56)
        font-weight: 900
        +xl
            font-size: 48px
        +md
            font-size: 40px
        +sm
            font-size: 48px
    h2
        font-size: 40px
        text-transform: uppercase
        line-height: 125%
        text-shadow: 0px 0px 15px rgba(226, 228, 255, 0.56)
        font-weight: 900
        +xl
            font-size: 36px
        +md
            font-size: 30px
        +sm
            font-size: 24px
.box-content-case-2
    width: 100%
    .welcome-text
        text-align: left
        transform: none
        margin: 0
        opacity: 1
        animation: none !important
        +md
            padding-left: 0
            padding-right: 0
        +lg
            h1
                font-size: 36px
            h2
                font-size: 28px
        +sm
            text-align: center
            h1
                font-size: 34px
            h2
                font-size: 24px
        +smm
            h1
                transform: translateX(-10%)
                opacity: 0
                transition: all .5s cubic-bezier(0.33, 1, 0.68, 1)
            h2
                transform: translateX(-10%)
                opacity: 0
                transition: all .5s cubic-bezier(0.33, 1, 0.68, 1)
.case-2-bg-title 
    position: relative
    margin-top: 60px
    +sm
        margin-top: -60px
    img
        width: 1955px
        position: absolute
        right: 0
        top: 50%
        transform: translateY(-50%)
        +xl
            width: 300%
        +sm
            position: relative
            top: 0
            transform: none
            left: -20px
            width: 110%
.case-2-items
    max-width: 1470px
    width: 100%
    margin-left: auto
    margin-right: auto
    margin-top: 200px
    position: relative
    z-index: 10
    +xl
        margin-top: 120px
        max-width: none
        padding-left: 100px
        padding-right: 100px
    +md
        padding-left: 0
        padding-right: 0
    +sm
        margin-top: 60px
.choose-case-header
    text-align: center
    .title
        text-transform: uppercase
        color: #E2E4FF
        font-weight: 900
        text-shadow: 0px 0px 15px rgba(226, 228, 255, 0.56)
        font-size: 20px
        margin-bottom: 10px
        +xl
            font-size: 18px
    .svg-image-arrow-down
        margin-left: auto
        margin-right: auto
        +xl
            width: 30px
.case-2-body
    position: relative
    &::after
        content: ''
        display: block
        padding-top: 65%
    .platform
        width: 100%
        left: 0
        position: absolute
        bottom: 0
        z-index: 2
    .effect
        width: 152%
        position: absolute
        left: 50%
        bottom: 6%
        transform: translateX(-50%)
        z-index: 1
        opacity: 0
        transition: all .3s cubic-bezier(0.33, 1, 0.68, 1)
    .light
        position: absolute
        left: 50%
        transform: translateX(-50%) scale(.5)
        z-index: 4
        pointer-events: none
        opacity: 0
        width: 165%
        top: -98%

        transition: opacity .3s cubic-bezier(0.33, 1, 0.68, 1), transform .5s cubic-bezier(0.33, 1, 0.68, 1)
    .case-2-obj
        position: absolute
        left: 0
        width: 100%
        bottom: -20%
        z-index: 5
        transition: all .3s cubic-bezier(0.33, 1, 0.68, 1)
.case-2-btn
    display: inline-block
    vertical-align: top
    position: relative
    
    // &::after
    //     content: ''
    //     position: absolute
    //     left: 20px
    //     right: 20px
    //     top: 10px
    //     bottom: 10px
    //     box-shadow: 0px 0px 35px #72D1FF, -15px 0px 35px #72D1FF, 15px 0 35px #72D1FF
    //     border-radius: 50%
.svg-image-btn
    position: relative
    margin-left: auto
    margin-right: auto
    display: block
    z-index: 15
    filter: drop-shadow(0px 4px 35px #72D1FF)
    z-index: 4
    opacity: 0
    pointer-events: none
    transition: all .3s linear
    +xl
        width: 230px
    span
        font-weight: 700
        display: block
        position: absolute
        left: 0
        right: 0
        top: 50%
        transform: translateY(-50%)
        text-transform: uppercase
        font-size: 23px
        +xl
            font-size: 18px
.case-2-footer 
    margin-top: 25px
    .case-2-name
        font-size: 45px
        line-height: 112%
        text-shadow: 0px 0px 15px rgba(226, 228, 255, 0.56)
        margin-top: 10px
        transform: scale(.8) translateY(-120px)
        transform-origin: top
        transition: all .3s cubic-bezier(0.33, 1, 0.68, 1) 
        +xl
            font-size: 30px
        +md
            font-size: 25px
    .case-2-sub-info
        font-size: 30px
        line-height: 112%
        margin-top: 10px
        opacity: 0
        pointer-events: none
        transform: translateY(20px)
        transition: all .5s cubic-bezier(0.33, 1, 0.68, 1) 
        +xl
            font-size: 25px
        +md
            font-size: 20px

.case-2-el
    position: relative

    .case-2-body
        +sm
            max-width: 270px
            width: 100%
            margin-left: auto
            margin-right: auto
    &:hover
        .svg-image-btn
            opacity: 1
            pointer-events: all
            transition-delay: .2s
        .case-2-footer 
            .case-2-name
                transform: scale(1) translateY(0)
            .case-2-sub-info
                opacity: 1
                transform: translateY(0)
                transition-delay: .3s
        
        .case-2-body
            .case-2-obj
                transform: translateY(-30%)
            .effect
                opacity: 1
            .light
                opacity: 1
                transform: translateX(-50%) scale(1)
.case-1-el
    position: relative
    cursor: pointer
    +sm
        padding-left: 40px
        padding-right: 40px
        max-width: 450px
        width: 100%
        margin-left: auto
        margin-right: auto
    +xs 
        max-width: 315px
        &.reverse 
            display: flex
            flex-direction: column-reverse
            .hand-wrapper
                margin-top: -30px
            .title
                font-size: 24px
            .case-info
                margin-top: 40px
            &:hover 
                .case-info 
                    margin-bottom: 0
                    transform: translateY(-40px)
    a
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 0
        display: none
        text-indent: -9999px
        cursor: pointer
    &.is-active
        a
            display: block
    &:hover
        .hand-wrapper
            transform: scale(1.2)
        .hand-obj
            transform: translateY(-17%)
        .hand-light
            opacity: 1
        .hand-arrow-light
            opacity: 1
            transform: translateY(0)
        .case-info
            transform: translateY(70px)
            +xl
                transform: translateY(40px)
            
        .sub-title
            opacity: 1
            pointer-events: all
            transform: translateY(0)

.xs-order-1
    +xs 
        order: -1

.hand-wrapper
    width: 424px
    position: relative
    left: -8%   
    transition: all .5s cubic-bezier(0.61, 1, 0.88, 1)
    &::after
        content: ''
        display: block
        padding-top: 95%
    +xl
        width: 100%
    +sm 
        left: 0
.hand-back
    position: absolute
    left: 0
    top: 0
    width: 100%
    z-index: 1
.hand-front
    position: absolute
    left: 0
    top: 0
    width: 100%
    z-index: 10
.hand-obj 
    position: absolute
    z-index: 5
    width: 79.5%
    left: 15.4%
    top: 0
    transition: all .5s cubic-bezier(0.5, 1, 0.89, 1)

    img
        width: 100%
.hand-light
    position: absolute
    left: -45%
    right: -45%
    top: -45%
    bottom: -45%
    z-index: 4
    pointer-events: none
    opacity: 0
    transition: all .5s linear 
    img
        width: 100%
        height: 100%
        left: 50%
        top: 50%
        transform: translate(-47%, -52%)
        position: absolute
.hand-arrow-light
    position: absolute
    right: 4%
    top: 4%
    width: 81.5%
    opacity: 0
    transition: opacity .25s linear, transform .4s linear 
    pointer-events: none
    transform: translateY(-40px)
    img
        width: 100%

.case-info
    transition: all .5s linear
    
    .title
        font-size: 45px
        line-height: 112%
        text-shadow: 0px 0px 15px rgba(226, 228, 255, 0.56)
        +xl
            font-size: 35px
        +md
            font-size: 28px
    .sub-title
        font-size: 35px
        line-height: 112%
        margin-top: 10px
        opacity: 0
        pointer-events: none
        transform: translateY(-40px)
        transition: all .5s linear 
        +xl
            font-size: 25px
        +md
            font-size: 20px
.welcome-text
    opacity: 0
    +sm
        opacity: 1
.case-1-el-col
    opacity: 0
    transform: scale(.6)
    pointer-events: none
    +sm
        opacity: 1
        transform: none
        pointer-events: all
        margin-bottom: 40px
        &:last-child
            margin-bottom: 0
    +xs 
        margin-top: -20px
        margin-bottom: 60px
.case-2-bg-title
    +smm
        opacity: 0
        transform: translateX(-10%)
        transition: all .5s linear 
.case-2-el-col
    
    +sm
        margin-bottom: 40px
        &:last-child
            margin-bottom: 0
    +smm
        opacity: 0
        transform: scale(.6)
        pointer-events: none
.is-loaded
    +smm
        .case-2-bg-title
            opacity: 1
            transform: translateX(0)
    .box-content-case-1
        .welcome-text
            animation: helloText 3s cubic-bezier(0.61, 1, 0.88, 1) forwards
            +sm
                animation: none
    .case-1-el-col, .case-2-el-col
        animation: helloCase 1s cubic-bezier(0.61, 1, 0.88, 1) 2s forwards
        +sm
            animation: none
    .box-content-case-2
        .welcome-text
            +smm
                h1
                    transform: translateX(0)
                    opacity: 1
                    transition-delay: .5s
                h2
                    transform: translateX(0)
                    opacity: 1
                    transition-delay: 1s
@keyframes helloText
    0%
        opacity: 0
        transform: translateY(200%) scale(.8)
    20%
        transform: translateY(200%) 
    60%
        opacity: 1
        transform: translateY(200%) scale(1)
    100%
        opacity: 1
        transform: translateY(0)
@keyframes helloCase
    0%
        opacity: 0
        transform: translateY(10%) scale(.8)
    100%
        opacity: 1
        transform: translateY(0)scale(1)
        pointer-events: all