:root {
  --yellow: #FFE600;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  background-color: #000;
  font-family: "FSElliotPro", Arial, sans-serif;
  font-weight: 700;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img, iframe {
  vertical-align: top;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.row-45 {
  margin-left: -45px;
  margin-right: -45px;
}
@media (max-width: 1024px) {
  .row-45 {
    margin-left: -10px;
    margin-right: -10px;
  }
}
.row-45 > [class*=col] {
  padding-left: 45px;
  padding-right: 45px;
}
@media (max-width: 1024px) {
  .row-45 > [class*=col] {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.row-15 {
  margin-left: -15px;
  margin-right: -15px;
}
@media (max-width: 1024px) {
  .row-15 {
    margin-left: -10px;
    margin-right: -10px;
  }
}
.row-15 > [class*=col] {
  padding-left: 15px;
  padding-right: 15px;
}
@media (max-width: 1024px) {
  .row-15 > [class*=col] {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (min-width: 768px) {
  .col-md-4 {
    flex: 0 0 auto;
    width: 33.33333333%;
  }
  .col-md-5 {
    flex: 0 0 auto;
    width: 41.66666667%;
    position: relative;
  }
  .col-md-7 {
    flex: 0 0 auto;
    width: 58.33333333%;
    position: relative;
  }
}
@media (max-width: 767px) {
  .col-md-4, .col-md-5, .col-md-7 {
    flex: 0 0 auto;
    width: 100%;
  }
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  height: 100%;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  padding-top: 120px;
  padding-bottom: 100px;
}
@media (max-width: 1600px) {
  .main {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.page-bg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.page-bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.paralax-bg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  mix-blend-mode: screen;
  opacity: 0.8;
}
@media (max-width: 767px) {
  .paralax-bg {
    opacity: 1;
  }
}

.paralax-bg-case-1 .layer {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute !important;
}
.paralax-bg-case-1 .layer img {
  width: 110%;
  height: 110%;
  object-fit: cover;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -25%);
}
@media (max-width: 767px) {
  .paralax-bg-case-1 .layer img {
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transform: none;
    position: absolute;
    display: block;
  }
}

.paralax-bg-case-2 .layer {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute !important;
}
.paralax-bg-case-2 .layer img {
  width: 110%;
  height: 110%;
  object-fit: cover;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 767px) {
  .paralax-bg-case-2 .layer img {
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transform: none;
    position: absolute;
    display: block;
  }
}

.decor-left {
  position: absolute;
