  }
}

.case-2-body {
  position: relative;
}
.case-2-body::after {
  content: "";
  display: block;
  padding-top: 65%;
}
.case-2-body .platform {
  width: 100%;
  left: 0;
  position: absolute;
  bottom: 0;
  z-index: 2;
}
.case-2-body .effect {
  width: 152%;
  position: absolute;
  left: 50%;
  bottom: 6%;
  transform: translateX(-50%);
  z-index: 1;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.33, 1, 0.68, 1);
}
.case-2-body .light {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) scale(0.5);
  z-index: 4;
  pointer-events: none;
  opacity: 0;
  width: 165%;
  top: -98%;
  transition: opacity 0.3s cubic-bezier(0.33, 1, 0.68, 1), transform 0.5s cubic-bezier(0.33, 1, 0.68, 1);
}
.case-2-body .case-2-obj {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: -20%;
  z-index: 5;
  transition: all 0.3s cubic-bezier(0.33, 1, 0.68, 1);
}

.case-2-btn {
  display: inline-block;
  vertical-align: top;
  position: relative;
}

.svg-image-btn {
  position: relative;
