.cookies-policy-banner
  background: #211d35 url(../images/cookies/cookie-bg.png) center -76px
  background-repeat: no-repeat
  bottom: 0
  height: auto
  overflow: hidden
  position: fixed
  width: 100%
  z-index: 200
  color: #fff
  font-weight: 400
  &__inner-wrapper
    align-items: center
    display: flex
    height: 100%
    margin: 0 auto
    max-width: 1290px
    padding: 0 15px
  &__text-block
    display: flex
    max-width: 906px
    width: 100%
    & .icon-wrap
      align-self: center
      max-height: 100px
      +md
        max-height: 80px
      +xs
        display: none
    & .text 
      align-content: baseline
      display: flex
      flex-wrap: wrap
      margin: 0 30px 0 30px
      max-width: 746px
      +md
        margin: 0 15px 0 15px
      +xs
        margin: 0 10px 0 0
      & .title
        font-size: 22px
        font-weight: 700
        margin-top: 15px
        +md
          font-size: 20px
        +xs
          font-size: 18px
        &p
          color: #ccabd8
          font-size: 15px
          margin: 5px 0
          font-weight: 400
          +md
            font-size: 14px
          &a
            color: #a560ff
  &__button-block
    align-items: center
    display: flex
    flex-grow: 1
    height: 100%
    justify-content: center
    & .accept-cookie-button
      align-items: center
      background-color: #5c49d0
      clip-path: polygon(12px 0, calc(100% - 12px) 0, 100% 50%, calc(100% - 12px) 100%, 12px 100%, 0 50%)
      color: #fff
      cursor: pointer
      display: inline-flex
      font-size: 16px
      font-weight: 700
      height: 56px
      justify-content: center
      min-width: 150px
      padding: 0 15px
      text-transform: uppercase
      transition: all 0.3s
      user-select: none
      white-space: nowrap
      width: unset
      border: none
      position: relative
      & .check-icon
        background-color: #fff
        display: inline-block
        height: 13px
        margin-left: 10px
        mask-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3E%3Cpath d='M20 4.095 17.986 2 7.554 12.848 2.014 7.05 0 9.145 7.554 17z'/%3E%3C/svg%3E")
        mask-position: center
        mask-repeat: no-repeat
        mask-size: 100%
        width: 18px

.button__content
  align-items: center
  display: flex
  flex-direction: row

