@use 'sass:math';

%svg-common {
	background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-arrow-down {
	@extend %svg-common;
	background-position: 28.09917355371901% 70.07874015748031%;
	background-size: math.div(283, 41)*100% auto;
	width: 41px;

}
@mixin svg-pos-image-arrow-down {
	background-position: 28.09917355371901% 70.07874015748031%;
}
@mixin svg-image-arrow-down-hover {
	.svg-image-arrow-down {
		@include svg-pos-image-arrow-down-hover
	}
}

.svg-image-arrow-down:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(25, 41)*100%;
}

.svg-image-arrow-down-dims {
	width: 41px;
	height: 25px;
}

